/* eslint-disable */
function toggleLinks(toggleButton, toggleElement) {
	const control = document.querySelector(`[aria-controls=${toggleElement}]`);
	const element = document.querySelector(
		`[aria-controlledby=${toggleButton}]`
	);

	control.onclick = (event) => {
		event.preventDefault();
		const elementIsHidden = element.getAttribute('aria-hidden') === 'true';

		// Find what the current state of the element should be
		let newElementState;
		if (elementIsHidden) {
			newElementState = {
				expanded: true,
				hidden: false,
			};
		} else {
			newElementState = {
				expanded: false,
				hidden: true,
			};
		}
		// Updating new element state
		control.setAttribute('aria-expanded', newElementState.expanded);
		element.setAttribute('aria-hidden', newElementState.hidden);
	};
}
/* eslint-enable */
